.grid{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 1fr 10fr 1fr;
    grid-template-areas: 
        ". heading ."
        ". week1-title ."
        ". week1-work .";
}
.heading{
    grid-area: heading;
}
h2.week1-title{
    grid-area: week1-title;
    display: grid;
    grid-template-columns: 1fr 1fr;
    cursor: pointer;
}
.week1-title i{
    justify-self: end;
}
.week1-work{
    grid-area: week1-work;
    border-left: 1px solid;
    border-right: 1px solid;
    border-bottom: 1px solid;
    padding: 0 10px 10px 10px;
    display: grid;
    grid-gap: 5px;
    grid-template-columns: 1fr;
    grid-template-areas: 
        'ex1'
        'week-work'
        'ex2'
        'week-work2'
        'ex3'
        'week-work3';
}

button.week1{
    grid-area: button;
    justify-self: start;
    align-self: center;
}
p.week1ex1{
    grid-area: ex1;
    display: grid;
    grid-template-columns: 5fr 1fr;
    cursor: pointer;
}
p.week1ex1 i{
    justify-self: end;
}

div#week1{
    grid-area: week-work;
}
.week1ex2{
    grid-area: ex2;
    display: grid;
    grid-template-columns: 5fr 1fr;
    cursor: pointer;
}
.week1ex2 i{
    justify-self: end;
}
#week1ex2-work{
    grid-area: week-work2;
    display: grid;
    grid-gap: 5px;
    grid-template-columns: 1fr 1fr;
}
.week1ex3{
    grid-area: ex3;
    display: grid;
    grid-template-columns: 5fr 1fr;
    cursor: pointer;
}
.week1ex3 i{
    justify-self: end;
}
#week1ex3-work{
    grid-area: week-work3;
    display: grid;
    grid-gap: 5px;
    grid-template-columns: 1fr 1fr;
}
#week1ex3-work img:nth-child(3){
    max-width: 500px;
}
